.advantages__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  padding: 120px 50px 60px;

  @include vp-1023 {
    padding: 80px 50px 40px;
  }

  @include vp-767 {
    grid-template-columns: 1fr;
    padding: 50px 25px 25px;
  }
}

.advantages__item {
  position: relative;
  min-height: 300px;
  padding: 40px 45px 35px;
  border: 1px solid #ebebeb;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 5px;
    height: 100%;
    background-color: #00b2ff;
  }

  @include vp-1023 {
    min-height: 360px;
    padding: 40px 43px 18px;

    &--short {
      min-height: 360px;

      h3 {
        margin: 0 0 14px;
      }
    }
  }

  @include vp-767 {
    min-height: 340px;
    padding: 30px 25px 18px 28px;

    &--short {
      min-height: 325px;

      h3 {
        margin: 0 0 14px;
      }
    }
  }

  &:hover {
    background-color: #00b2ff;

    & h3,
    p,
    svg {
      color: #f2f5f7;
      fill: #f2f5f7;
    }
  }
}

.advantages__icon {
  margin-bottom: 13px;
  fill: #00b2ff;

  @include vp-1023 {
    margin-bottom: 8px;
  }

  @include vp-767 {
    width: 30px;
    height: 30px;

    &--short {
      margin-bottom: 11px;
    }
  }
}

.advantages__item h3 {
  margin: 0 0 13px;
  font-family: $ff-secondary;
  font-weight: 400;
  font-size: 24px;
  line-height: 34px;
  color: #00b2ff;

  @include vp-1023 {
    margin: 0 0 8px;
  }

  @include vp-767 {
    font-size: 20px;
    line-height: 28px;
  }
}

.advantages__item p {
  margin: 0 0 10px;
  font-family: $ff-primary;
  font-size: 16px;
  line-height: 22px;

  @include vp-767 {
    font-size: 14px;
    line-height: 19px;
  }
}
